import styled, { ThemeProvider, createGlobalStyle } from "styled-components";
import Router from "./Router";
import { dark, light, } from "./theme";
import { useDarkMode } from "./modetoggle/DarkMode";
import Toggle from "./modetoggle/Toggle";
import Home from "./modetoggle/home";

const GlobaStyle = createGlobalStyle`
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+3:wght@300&display=swap');
/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}
/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
    display: none;
}
body {
  line-height: 1;
}
menu, ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
* {
  box-sizing: border-box;
}
body {
  font-weight: 300;
  font-family: "Source Sans 3", sans-serif;
  background-color: ${props => props.theme.bgColor};
  color:${(props) => props.theme.textColor};
  line-height: 1.2;
}
a {
  text-decoration: none;
  color:inherit;
}
`;

function App() {
  const [thememode, toggleTheme] = useDarkMode();
  const theme =
    thememode === "light" ? { mode: light} : { mode: dark};
  return (
    <>
      <ThemeProvider theme={theme}>        
        <Toggle thememode={thememode} toggleTheme={toggleTheme} />
        <Home />
        <Backgound>
          <GlobaStyle />
          <Router />
        </Backgound>
      </ThemeProvider>
    </>
  );
}

const Backgound = styled.div`
  background-color: ${({ theme }) => theme.mode.mainBackground};
  height: 100vh;
`;

export default App;